@font-face {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-display: fallback;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-display: fallback;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
html {
  font-family: "Oswald", sans-serif;
  -webkit-font-smoothing: antialiased;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
link {
  box-sizing: border-box;
}
p {
  margin: 0;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
fieldset {
  margin-inline-start: 0;
  margin-inline-end: 0;
  padding-block-start: 0;
  padding-inline-start: 0;
  padding-inline-end: 0;
  padding-block-end: 0;
  border: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
::after,
::before {
  box-sizing: border-box;
}
.page {
  height: 100%;
  font-family: "Arial";
  font-size: 14px;
  line-height: 18px;
}
.page__body {
  height: 100%;
  margin: 0;
  color: #000000;
}
.page__body--wrapper {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  background-color: #ffffff;
}
.container {
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
  .container--form {
    padding-left: 71px;
    padding-right: 67px;
  }
}
@media (min-width: 1440px) {
  .container {
    max-width: 1220px;
    padding-left: 110px;
    padding-right: 110px;
  }
}
.button {
  text-align: center;
  text-transform: uppercase;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 18px;
  border: 0;
  background-color: #68B738;
  color: #ffffff;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.page-header {
  display: grid;
  grid-template-columns: 38px 1fr 38px;
  grid-template-rows: min-content auto;
  padding-top: 12px;
  padding-bottom: 17px;
  padding-left: 20px;
  padding-right: 20px;
}
.page-header__logo {
  display: flex;
  align-items: center;
  grid-column-start: 1;
  grid-column-end: 3;
}
.page-header__logo--picture {
  margin-right: 4px;
}
.page-header__logo--image {
  width: 38px;
  height: 38px;
}
.page-header__logo--text {
  display: flex;
  flex: 1 1 auto;
  justify-content: center;
}
.page-header__logo:hover {
  opacity: 80%;
  transition: all 0.3s ease-out;
}
.page-header__logo:active {
  opacity: 60%;
  transition: all 0.3s ease-out;
}
@media (min-width: 768px) {
