.main-nav {
  position: relative;
  display: grid;
  grid-row-start: 1;
  grid-row-end: 4;
  grid-column-start: 1;
  grid-column-end: 4;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
}
.main-nav__list {
  background-color: #ffffff;
}
.main-nav__item {
  position: relative;
  text-align: center;
}
.main-nav__link {
  display: block;
  padding-top: 22px;
  padding-bottom: 22px;
  padding-right: 22px;
  padding-left: 22px;
  color: #000000;
}
.main-nav__link:hover {
  opacity: 60%;
  transition: all 0.3s ease-out;
}
.main-nav__link:active {
  opacity: 30%;
  transition: all 0.3s ease-out;
}
@media (max-width: 767px) {
  .main-nav__toggle {
    position: absolute;
    justify-self: end;
    width: 24px;
    height: 38px;
    background-color: #ffffff;
    border: 0;
    cursor: pointer;
  }
  .main-nav--closed .main-nav__toggle::before {
    position: absolute;
    top: calc(50% - 1px);
    right: 0;
    width: 24px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 -8px 0 0 #000000, 0 8px 0 0 #000000;
    transition: all 0.3s ease-out;
    content: "";
  }
  .main-nav--opened .main-nav__toggle {
    background-color: transparent;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    position: absolute;
    top: calc(50% - 1px);
    right: 0;
    width: 24px;
    height: 2px;
    background-color: #000000;
    content: "";
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--opened .main-nav__list {
    position: absolute;
    top: 54px;
    left: -20px;
    display: block;
    width: calc(40px + 100%);
    z-index: 1;
  }
  .main-nav__item::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    border-top: 1px solid #E6E6E6;
    content: "";
  }
  .main-nav__item:last-child:after {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    border-top: 1px solid #E6E6E6;
    content: "";
  }
}
@media (min-width: 768px) {
  .main-nav {
    flex: 1 1 auto;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    padding-top: 7px;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    justify-content: flex-end;
    margin-right: -17px;
  }
  .main-nav__link {
    padding-top: 16px;
    padding-bottom: 18px;
    padding-right: 17px;
    padding-left: 17px;
  }
  .main-nav__link--current {
    position: relative;
  }
  .main-nav__link--current::before {
    position: absolute;
    width: calc(-36px + 100%);
    left: 50%;
    bottom: 8px;
    height: 2px;
    transform: translateX(-50%);
    background-color: #68B738;
    content: "";
  }
  .main-nav__link--index.main-nav__link--current::before {
    position: absolute;
    width: calc(-36px + 100%);
    left: 50%;
    bottom: 8px;
    height: 2px;
    transform: translateX(-50%);
    background-color: #68B738;
    content: "";
  }
}
@media (min-width: 1440px) {
  .main-nav {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    padding-top: 0;
  }
  .main-nav__list {
    background-color: transparent;
  }
  .main-nav__link--index {
    color: #ffffff;
  }
  .main-nav__link--index.main-nav__link--current::before {
    background-color: #ffffff;
  }
}
